const API = require("../../utils/api");

export default {
  data() {
    return {
      page: 1,
      category: '',
      isBottom: false,
      isLastPage: false
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    this.getCategories();
  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {},

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {
    this.setData({
      page: 1,
      category: '',
      isBottom: false
    });
    this.getCategories();
  },

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {
    this.setData({
      isBottom: true
    });

    if (!this.isLastPage) {
      this.getCategories({
        page: this.page
      });
    }
  },

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {
    getCategories: function () {
      API.getCategories().then(res => {
        let args = {};

        if (res.length < 10) {
          this.setData({
            isLastPage: true
          });
        }

        if (this.isBottom) {
          wx.showToast({
            title: '加载下一页',
            icon: 'loading',
            duration: 1000
          });
          args.category = [].concat(this.category, res);
          args.page = this.page + 1;
        } else {
          args.category = res;
          args.page = this.page + 1;
        }

        this.setData(args);
        wx.stopPullDownRefresh();
      }).catch(err => {
        console.log(err);
        wx.stopPullDownRefresh();
      });
    },
    bindCateByID: function (e) {
      let id = e.currentTarget.id;
      wx.navigateTo({
        url: '/pages/list/list?id=' + id
      });
    }
  }
};
